
import React, { useEffect, useState } from 'react';
import './Gridmodule.css';

const GridLayout = ({
  paginatedData,
  numPostsToShow,
  showImgToggler,
  showPostTitle,
  showMeta,
  showContent,
  contentType,
  truncateContent,
  wordsLimit,
  imageUrl,
  valueForWidth,
  widthPercentage,
  widthController,
  ImgWidth,
  height,
  columnGap,
  rowGap,
  totalColoms,
  ReadMoreBgColor,
  showReadMoreToggler,
  ReadMoreTextColor,
  button_Top_bottom_Padding,
  button_Right_Left_Padding,
  title_font_size,
  Meta_font_size,
  Content_font_size,
  Read_more_font_size,
  __,
  customCSS,
  title_Manage_styling,
  Content_Manage_styling,
  ReadMoreText,
  Meta_Manage_styling
}) => {


  const GridItems = paginatedData.slice(0, numPostsToShow);



  const getGridTemplateColumns = (totalColoms) => {
    const width = window.innerWidth;
    if (width >= 992 && width < 1200) {
      return `repeat(${totalColoms || 1}, 1fr)`;
    } else if (width >= 1200) {
      return `repeat(${totalColoms || 1}, 1fr)`;
    }
  };
  const [gridColumns, setGridColumns] = useState(getGridTemplateColumns());
  useEffect(() => {
    const handleResize = () => {
      setGridColumns(getGridTemplateColumns(totalColoms));
    };
    window.addEventListener('resize', handleResize);
    setGridColumns(getGridTemplateColumns(totalColoms));
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [totalColoms]);
  return (
    <div
      className='grid-layout'
      style={{
        columnGap: columnGap || '20px',
        rowGap: rowGap || '20px',
        gridTemplateColumns: gridColumns,

      }}
    >
      {customCSS && <style>{customCSS}</style>}
      {GridItems.map((item) => {
        const formattedDate = new Date(item?.date).toLocaleDateString("en-US", {
          year: "numeric",
          month: "long",
          day: "numeric",
        });

        let featuredImgExist = false;
        if (item.hasOwnProperty("featured_image_url")) {
          const featuredImageUrlObj = item.featured_image_url;
          if (
            featuredImageUrlObj &&
            typeof featuredImageUrlObj === "object" &&
            Object.keys(featuredImageUrlObj).length > 0
          ) {
            featuredImgExist = true;
          }
        }

        return (
          <div key={item.id} className='post-container'>
            {featuredImgExist && <div className='image-container'>
              {showImgToggler && item?.featured_image_url && (
                <>
                  <img
                    className='block-image'
                    src={item.featured_image_url[imageUrl]}
                    style={{
                      ...(valueForWidth
                        ? { maxWidth: widthPercentage, maxHeight: widthPercentage }
                        : {
                          maxWidth: widthController === true ? ImgWidth : '',
                          maxHeight: widthController === true ? height : '',
                        }),
                    }}
                    alt={item.title.rendered} // Added alt text for accessibility
                  />
                  {item.assigned_categories && (
                    <span
                      className='category-tag'
                      style={{
                        position: 'absolute',
                        bottom: '10px',
                        left: '10px',
                        backgroundColor: 'rgba(0, 0, 0, 0.5)',
                        color: 'white',
                        padding: '5px 10px',
                        borderRadius: '4px',
                      }}
                    >
                      {item.assigned_categories}
                    </span>)}
                </>
              )}
            </div>}
            {showPostTitle && (
              <div className='post-title' 
              style={{
                ...(title_Manage_styling && {
                  fontSize: title_font_size,
                })
              }}
              >
                <a
                  className='title'
                  href={item.link}
                  dangerouslySetInnerHTML={{ __html: item?.title.rendered }}                
                ></a>
              </div>
            )}

            {showMeta && (
              <div className='meta-data' 
              
              style={{
                ...(Meta_Manage_styling && {
                  fontSize: Meta_font_size,
                })
              }}
              >
                <p>
                  {formattedDate && `${formattedDate} `}
                  {item.post_author && `| Author: ${item.post_author}`}
                </p>
              </div>
            )}

            {showContent && (
              <div
              style={{
                ...(Content_Manage_styling && {
                  fontSize: Content_font_size,
                })
              }}
                className='post-content'
                dangerouslySetInnerHTML={{
                  __html:
                    contentType === "Full-Post"
                      ? item?.content?.rendered
                      : !item?.excerpt?.rendered
                        ? truncateContent(item?.content?.rendered, wordsLimit)
                        : truncateContent(item?.excerpt?.rendered, wordsLimit),
                }}
              />
            )}

           
          <div className='read-more-btn'  >
              <a
                href={item.link}
                target="_blank"
                className='read-more-button'
                style={{
                  ...(showReadMoreToggler && {
                    backgroundColor: ReadMoreBgColor,
                    color: ReadMoreTextColor,
                    padding: `${button_Top_bottom_Padding} ${button_Right_Left_Padding}`,  
                    fontSize: Read_more_font_size,           
                  }),
                }}
                >
                   {__(ReadMoreText, "advanced-posts-listing")}
                
              </a>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default GridLayout;
