
import React, { useEffect, useState } from 'react';
import './OverlayLayout.css';

const OverlayLayout = ({
  paginatedData,
  numPostsToShow,
  showImgToggler,
  showPostTitle,
  showMeta,
  showContent,
  contentType,
  truncateContent,
  wordsLimit,
  imageUrl,
  valueForWidth,
  widthPercentage,
  widthController,
  ImgWidth,
  height,
  columnGap,
  rowGap,
  totalColoms,
  ReadMoreBgColor,
  showReadMoreToggler,
  ReadMoreTextColor,
  button_Top_bottom_Padding,
  button_Right_Left_Padding,
  __, // Placeholder for translation function
  title_font_size,
  Meta_font_size,
  Content_font_size,
  Read_more_font_size,
  customCSS,
  Content_Manage_styling,
  title_Manage_styling,
  Meta_Manage_styling,
  ReadMoreText,
}) => {
  const overlayData = paginatedData.slice(0, numPostsToShow);

  // Function to calculate column width based on window width
  const getOverlayTemplateColumns = (totalColoms) => {
    const widthh = window.innerWidth;
    if (widthh >= 992 && widthh < 1200) {
      return `calc(${100 / totalColoms}% - 20px)`;
    } else if (widthh >= 1200) {
      return `calc(${100 / totalColoms}% - 20px)`;
    } else {
      return '100%'; // Fallback for smaller screens
    }
  };

// State to manage overlay columns
  const [overlayColumns, setOverlayColumns] = useState(getOverlayTemplateColumns(totalColoms));

  useEffect(() => {
    const handleResize = () => {
      setOverlayColumns(getOverlayTemplateColumns(totalColoms));
    };

    window.addEventListener('resize', handleResize);
    // Initial setting
    setOverlayColumns(getOverlayTemplateColumns(totalColoms));

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [totalColoms]);

  return (
    <div
      className='overlay-layout'
      style={{
        columnGap: columnGap || '20px',
        rowGap: rowGap || '20px',    
      }}
    >
     
      {customCSS && <style>{customCSS}</style>}

      {overlayData.map((item) => {
        const formattedDate = new Date(item?.date).toLocaleDateString("en-US", {
          year: "numeric",
          month: "long",
          day: "numeric",
        });

        let featuredImgExist = false;
        if (item.hasOwnProperty("featured_image_url")) {
          const featuredImageUrlObj = item.featured_image_url;
          if (
            featuredImageUrlObj &&
            typeof featuredImageUrlObj === "object" &&
            Object.keys(featuredImageUrlObj).length > 0
          ) {
            featuredImgExist = true;
          }
        }

        return (
          <div key={item.id} className='post-container' style={{ width: overlayColumns }}>
            {showImgToggler && (
              <div
                className='image-container'
                style={{
                  backgroundImage: `url(${item.featured_image_url[imageUrl]})`,
                  ...(valueForWidth
                    ? { maxWidth: widthPercentage, maxHeight: widthPercentage }
                    : {
                      maxWidth: widthController === true ? ImgWidth : '',
                      maxHeight: widthController === true ? height : '',
                    }),
                }}
              >
                <div className='overlay'>
                  <div className='overlay-content'>
                    {showPostTitle && (
                      <div className='post-title' style={{
                        ...(title_Manage_styling && {
                          fontSize: title_font_size,
                        })
                      }}>
                        <a
                          className='title'
                          href={item.link}
                          dangerouslySetInnerHTML={{ __html: item?.title.rendered }}
                        ></a>
                      </div>
                    )}

                    {showMeta && (
                      <div className='meta-data' style={{
                        ...(Meta_Manage_styling && {
                          fontSize: Meta_font_size,
                        })
                      }}>
                        <p>
                          {formattedDate && `${formattedDate} `}
                          {item.post_author && `| Author: ${item.post_author} `}
                          {item.assigned_categories && `| Categories: ${item.assigned_categories} `}
                        </p>
                      </div>
                    )}

                    {showContent && (
                      <div
                        style={{
                          ...(Content_Manage_styling && {
                            fontSize: Content_font_size,
                          })
                        }}
                        className='post-content'
                        dangerouslySetInnerHTML={{
                          __html:
                            contentType === "Full-Post"
                              ? item?.content?.rendered
                              : !item?.excerpt?.rendered
                                ? truncateContent(item?.content?.rendered, wordsLimit)
                                : truncateContent(item?.excerpt?.rendered, wordsLimit),
                        }}
                      />
                    )}


                    <div className='read-more-btn'  >
                      <a
                        href={item.link}
                        target="_blank"
                        className='read-more-button'
                        style={{
                          ...(showReadMoreToggler && {
                            backgroundColor: ReadMoreBgColor,
                            color: ReadMoreTextColor,
                            padding: `${button_Top_bottom_Padding} ${button_Right_Left_Padding}`,  
                            fontSize: Read_more_font_size, 
                           
                          }),
                        }}
                      >
                        {__(ReadMoreText, "advanced-posts-listing")}

                      </a>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
};

export default OverlayLayout;