
import React, { useEffect, useState } from 'react';
import './MasanoryLayoutmodule.css';

const MasanoryLayout = ({
  paginatedData,
  numPostsToShow,
  showImgToggler,
  showPostTitle,
  showMeta,
  showContent,
  contentType,
  truncateContent,
  wordsLimit,
  imageUrl,
  valueForWidth,
  widthPercentage,
  widthController,
  ImgWidth,
  height,
  columnGap,
  rowGap,
  totalColoms,
  ReadMoreBgColor,
  showReadMoreToggler,
  ReadMoreTextColor,
  button_Right_Left_Padding,
  button_Top_bottom_Padding,
  title_font_size,
  Meta_font_size,
  Content_font_size,
  Read_more_font_size,
  customCSS,
  __, // Placeholder for translation function
  Content_Manage_styling,
  title_Manage_styling,
  Meta_Manage_styling,
  ReadMoreText
}) => {
  const [columnCount, setColumnCount] = useState(1);

  const updateColumnCount = () => {
    const width = window.innerWidth;
    let columns = totalColoms || 1;

    if (width <= 575) {
      columns = 1; // Mobile screens
    } else if (width >= 576 && width < 992) {
      columns = 2; // Small screens
    } else if (width >= 992 && width < 1200) {
      columns = totalColoms || 3; // Medium screens
    } else if (width >= 1200) {
      columns = totalColoms || 4; // Large screens
    }

    setColumnCount(columns);
  };

  useEffect(() => {
    updateColumnCount(); // Set initial column count
    window.addEventListener('resize', updateColumnCount); // Update column count on resize

    return () => {
      window.removeEventListener('resize', updateColumnCount); // Clean up the event listener
    };
  }, [totalColoms]);

  const masonry = paginatedData.slice(0, numPostsToShow);

  return (
    <div className='masonry-layout'
      style={{
        columnGap: columnGap || '20px', // Inline column gap with default value

        columnCount: columnCount,
      }}
    >
      {customCSS && <style>{customCSS}</style>}
      {masonry.map((item) => {
        const formattedDate = new Date(item?.date).toLocaleDateString("en-US", {
          year: "numeric",
          month: "long",
          day: "numeric",
        });

        let featuredImgExist = false;
        if (item.hasOwnProperty("featured_image_url")) {
          const featuredImageUrlObj = item.featured_image_url;
          if (
            featuredImageUrlObj &&
            typeof featuredImageUrlObj === "object" &&
            Object.keys(featuredImageUrlObj).length > 0
          ) {
            featuredImgExist = true;
          }
        }

        return (
          <div key={item.id} className='post-container' style={{
            marginBottom: rowGap || '20px', // Inline column gap with default value

          }}>
            {showImgToggler && item?.featured_image_url && featuredImgExist && (
              <div className='image-container'>
                <img
                  className='block-image'
                  src={item.featured_image_url[imageUrl]}
                  style={{
                    ...(valueForWidth
                      ? { maxWidth: widthPercentage, maxHeight: widthPercentage }
                      : {
                        maxWidth: widthController === true ? ImgWidth : '',
                        maxHeight: widthController === true ? height : '',
                      }),
                  }}
                />
                {item.assigned_categories && (
                  <span
                    className='category-tag'
                    style={{
                      position: 'absolute',
                      bottom: '10px',
                      left: '10px',
                      backgroundColor: 'rgba(0, 0, 0, 0.5)',
                      color: 'white',
                      padding: '5px 10px',
                      borderRadius: '4px',
                    }}
                  >
                    {item.assigned_categories}
                  </span>
                )}
              </div>
            )}

            {showPostTitle && (
              <div className='post-title'    style={{
                ...(title_Manage_styling && {
                  fontSize: title_font_size,
                })
              }}>
                <a
                  className='title'
                  href={item.link}
                  dangerouslySetInnerHTML={{ __html: item?.title.rendered }}
                 
                ></a>
              </div>
            )}

            {showMeta && (
              <div className='meta-data'   style={{
                ...(Meta_Manage_styling && {
                  fontSize: Meta_font_size,
                })
              }}>
                <p>
                  {formattedDate && `${formattedDate} `}
                  {item.post_author && `| Author: ${item.post_author} `}
                  {item.assigned_categories && `| Categories: ${item.assigned_categories} `}
                </p>
              </div>
            )}

            {showContent && (
              <div
              style={{
                ...(Content_Manage_styling && {
                  fontSize: Content_font_size,
                })
              }}                className='post-content'
                dangerouslySetInnerHTML={{
                  __html:
                    contentType === "Full-Post"
                      ? item?.content?.rendered
                      : !item?.excerpt?.rendered
                        ? truncateContent(item?.content?.rendered, wordsLimit)
                        : truncateContent(item?.excerpt?.rendered, wordsLimit),
                }}
              />
            )}
          
          <div className='read-more-btn'  >
              <a
                href={item.link}
                target="_blank"
                className='read-more-button'
                style={{
                  ...(showReadMoreToggler && {
                    backgroundColor: ReadMoreBgColor,
                    color: ReadMoreTextColor,
                    padding: `${button_Top_bottom_Padding} ${button_Right_Left_Padding}`,  
                    fontSize: Read_more_font_size,          
                  }),
                }}
                >
                  {__(ReadMoreText, "advanced-posts-listing")}
                
              </a>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default MasanoryLayout;
